.App {
  text-align: center;
}

.App-header {
  background-color: #64b006;
  height: 100px;
  padding: 20px;
  color: white;
}

.App-calculator {
  font-size: large;
  text-align: center;
}

.inputs {
  width: 80%;
  display: inline-block;
  padding: 50px;
  text-align: left;
}

.form-control {
  background-color: #FAF4A8;
  max-width: 500px;
}

.results-row {
  -ms-flex-line-pack: center;
      align-content: center;
  font-size: large;
  width: 60%;
  margin-left: 1em;
  text-align: center;
}

.title {
  width: 100%;
  display: inline-block;
  text-align: right;
}

.value {
  width: 100%;
  display: inline-block;
  text-align: left;
}

.well {
  background: #c0e559;
}

.form-center {
  display: inline-block;
  width: 100%;
}

.right-align {
  text-align: right;
}

.left-align {
  text-align: left;
  padding-left: 8em;
}

h2 {
  font-weight: 500;
}

h3 {
  font-weight: 500;
}


